@import url(https://fonts.googleapis.com/css?family=Alfa+Slab+One);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  background-color: #f3f2f1;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  text-align: center;
  font-size: calc(10px + 2vmin);
  color: black;
  font-family: sans-serif;
  /* background-image: url("images/background.jpg");
  background-size: contain; */
}

.App-header {
  min-height: 100vh;
}

.App-welcome {
  display:flex;
  justify-content: center;
}

#aboutme {
  font-size: x-large;
  margin: 0% 25% 0% 25%;
}

#name {
  font-family: 'Alfa Slab One', cursive;
  border-right: solid 3px rgba(0, 0, 0, 0.75);
  white-space: nowrap;
  overflow: hidden;
}

#profile {
  border-radius: 50%;
  margin-top: 40px;
  margin-bottom:20px;
  height: 180px;
  width: auto;
}

#projects {
  font-size: medium;
  margin: 5% 10% 5% 10%;
  display: flex;
}

#projects div{
  border-radius: 15px;
  padding: 10px;
  margin: 10px;  
}

/* Animations */

.App-header, #profile, #projects{
  opacity: 0;
  -webkit-animation: fadeInAnimation 1.5s ease 0s 1 forwards;
          animation: fadeInAnimation 1.5s ease 0s 1 forwards;
}

#name {
  -webkit-animation: animated-text 1.25s linear 1s 1 normal both,
             animated-cursor 1s steps(29,end) infinite;
          animation: animated-text 1.25s linear 1s 1 normal both,
             animated-cursor 1s steps(29,end) infinite;
}

@-webkit-keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

/* text animation */
@-webkit-keyframes animated-text{
  from{width: 0;}
  to{width: 6.7em;}
}
@keyframes animated-text{
  from{width: 0;}
  to{width: 6.7em;}
}

/* cursor animations */
@-webkit-keyframes animated-cursor{
  from{border-right-color: rgba(0, 0, 0, 0.75);}
  to{border-right-color: transparent;}
}
@keyframes animated-cursor{
  from{border-right-color: rgba(0, 0, 0, 0.75);}
  to{border-right-color: transparent;}
}
#brand {
    font-family: 'Alfa Slab One', cursive;
    margin-left: 1%;
    font-size: xx-large;
}

.navlink {
    display: inline-flex;
}

.tooltiptext {
    font-size: small;
}

.navlink .tooltiptext {
    visibility: hidden;
    width: 6em;
    background-color: black;
    color: #fff;
    border-radius: 6px;
    padding: 5px 0;
    position: absolute;
    z-index: 1;
    top: 90%;
    margin: 0% -1.2% 0%;
}

.navlink .tooltiptext::after {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent black transparent;
}
  
.navlink:hover .tooltiptext {
    visibility: visible;
}
